.card {
  position:relative;
  font-size: 1.5rem;
  
}
  
.card  section{
  width: 100%;
  height:40px;
  padding: 5px;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}
  
  
.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position:absolute;
}
  
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}
  
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
  
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}